
import Loading from 'vux/src/components/loading/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import { mapState, mapActions } from 'vuex'
export default {
data() {
return {
transitionName: 'slide-left',
}
},
components: {
Loading,ViewBox,Tabbar, TabbarItem,
},
methods: {
...mapActions([
'updateDemoPosition'
])
},
computed: {
isIndex() {
return /index/.test(this.$route.path)
},
isMy() {
return /my/.test(this.$route.path)
},
isInteraction() {
return /interaction/.test(this.$route.path)
},
isCheck() {
return /check/.test(this.$route.path)
},
...mapState({
route: state => state.route,
path: state => state.route.path,
deviceready: state => state.app.deviceready,
demoTop: state => state.vux.demoScrollTop,
isLoading: state => state.vux.isLoading,
direction: state => state.vux.direction,
isTabbaerShow: state => state.vux.isTabbaerShow
}),
},
created() {
console.log(this.$store.state.isTabbaerShow)
},
beforeRouteUpdate(to, from, next) {
let isBack = this.$router.isBack
if (isBack) {
this.transitionName = 'slide-right'
} else {
this.transitionName = 'slide-left'
}
this.$router.isBack = false
next()
}
}



